  <script>
  import {Error} from '../class/Error.js'
  import Papa from 'papaparse';
  import csvFile_Rus from '../../public/data/data_Russian.csv'; 
  import csvFile_Cze from '../../public/data/data_Czech.csv'; 
  
  // let select_content = ref(''); //選取內容
  let errors = []
  let selectionObj = window.getSelection();
  console.log(selectionObj.toString());
  let selectedText = '';
  let absolutePosition = {x1:0,x2:0,y1:0,y2:0};
  let range = new Range();
  let errordict = {'Spelling Error':'#FE5F55','Syntax Error':'#59B3D9','Morphological Error':'#659157','Lexical Error':'#84569F','Pronunciation Error':'#EEC643','Punctuation Error':'#de177a','Stylistic Error':'#FAA00F','Logical Error':'#4F3824','Factual Error':'#2A4494'};
  let log_start = 0;
  let log_end = 0;
  let changeLog = [];
  let lastAdjustedEnd = 0;


  async function loadCSVfile(file){
    let json = new Promise((resolve, reject) =>{
        Papa.parse(file, {
            download: true,
            header: true,
            dynamicTyping: true,
            complete: (results) => {
               resolve(results);
            },
            error: (error) => {
                reject(error);
            },
          });
    })
    let results = await json;
    return results.data;

    }

    async function preprocessText() {
        
        this.RusData = await this.loadCSVfile(csvFile_Rus); // 載入第一個 CSV 文件
        this.RusID = this.RusData.map((item) => item.id);
        this.CzeData = await this.loadCSVfile(csvFile_Cze); // 載入第二個 CSV 文件
        this.CzeID = this.CzeData.map((item) => item.id);

        this.rawContent = this.RusData[0].text;
        this.doc_name = this.RusData[0].id;

    }

    function getRowData(up_down) {
        if(this.doc_name[0] === "R"){
            if (up_down === 0){
                if (this.currentRowIndex > 0){
                    this.currentRowIndex--;
                }
                else {
                    return;
                }
            }
            else {
                if (this.currentRowIndex < this.RusData.length){
                    this.currentRowIndex++;
                }
                else {
                    return;
                }
            }
            this.rawContent = this.RusData[this.currentRowIndex].text;
            this.oriContent = this.rawContent;
            this.doc_name = this.RusData[this.currentRowIndex].id;
        } else {
            if (up_down === 0){
                if (this.currentRowIndex > 0){
                    this.currentRowIndex--;
                }
                else {
                    return;
                }
            }
            else {
                if (this.currentRowIndex < this.CzeData.length){
                    this.currentRowIndex++;
                }
                else {
                    return;
                }
            }
            this.rawContent = this.CzeData[this.currentRowIndex].text;
            this.oriContent = this.rawContent;
            this.doc_name = this.CzeData[this.currentRowIndex].id;
        } 
    }

  function seeOri() { // unknownfunction
      if(this.isOri){
          this.isOri = false;
          this.rawContent = this.tempContent;
      }else{
          this.isOri = true;
          this.tempContent = this.rawContent + "hello";
      }
  }
  
  function transContent() {
      this.editOrOK = 0;
      this.ori_content = this.rawContent;
      this.view = 1;
      console.log(this.view);
      this.view = 1;
  }
  
  function editContent() {
      let confirm = window.confirm('編輯語料會清除已標記錯誤，確定要編輯嗎？');
      if (confirm == true) {
          this.errors = [];
          this.no_error = true;
          this.editOrOK = 1;
          this.view = 0;
          console.log(this.view);
          this.view = 0;
          this.rawContent = this.ori_content;
          changeLog = [];
      }
  }
  
  function mySearchLabel() {
    if(this.doc_name_input[0] === "R"){
        if(this.RusID.indexOf(this.doc_name_input) === -1){
          alert('no such document');
          this.doc_name_input = "";
          return;
      }else{
        this.doc_name = this.doc_name_input;
        this.rawContent = this.RusData[this.RusID.indexOf(this.doc_name)].text;
        this.ori_content = this.rawContent;
        this.doc_name_input = "";
        this.currentRowIndex = this.RusID.indexOf(this.doc_name);
      }
    }else if(this.doc_name_input[0] === "C"){
        if(this.CzeID.indexOf(this.doc_name_input) === -1){
          alert('no such document');
          this.doc_name_input = "";
          return;
      }else{
        this.doc_name = this.doc_name_input;
        this.rawContent = this.CzeData[this.CzeID.indexOf(this.doc_name)].text;
        this.ori_content = this.rawContent;
        this.doc_name_input = "";
        this.currentRowIndex = this.CzeID.indexOf(this.doc_name);
      }
    }else{
        alert('no such document');
        this.doc_name_input = "";
        return; 
      }
  }

  function saveContent() {
    this.RusData[this.currentRowIndex].text;
    this.RusData[this.currentRowIndex].text;
    this.showPopup = false;
    this.csv_revision.push([this.doc_name, this.rawContent, this.updatedText]);
    this.errors = [];
    lastAdjustedEnd = 0;
    changeLog = [];
  }

  function preStep() {
  
  }
  function nextStep() {
  
  }
  
  function newError(){

    changeLog.push({
    original: selectedText,
    replacement: this.fixedword,
    start: log_start,
    end: log_end
    });
  
      if(this.selectedError === '' || this.fixedword === '' || this.selectedText === ''){
          alert('missing value');
      }
      else{
          let error = new Error(absolutePosition, selectedText, this.fixedword, this.selectedError);
          this.errors.push(error);
          console.log(errors);

          this.csv_error.push([this.doc_name, this.selectedError, selectedText, this.fixedword])
  
          const span = document.createElement('span');
          span.style.textDecoration = 'underline';
          span.style.textDecorationThickness = '3px';
          span.style.textDecorationColor = errordict[this.selectedError];
          span.style.textUnderlineOffset = '4px';
          range.surroundContents(span);
  
          this.no_error = false;
          this.menuX = absolutePosition.x1;
          this.menuY = absolutePosition.y1;
          this.isMenuVisible = true;
          this.fixedword = "";
          this.selectedError = "";
      }
  }

  function openPopup(){
    this.showPopup = true;
    let updated_str = this.rawContent.toString();
    this.updatedText = updated_str;
    let offset = 0;
    for (const change of changeLog) {
        const adjustedStart = change.start + offset + lastAdjustedEnd;
        const adjustedEnd = change.end + offset + lastAdjustedEnd;
        // console.log('adjusted', adjustedStart, adjustedEnd);
        this.updatedText = this.updatedText.substring(0, adjustedStart) + change.replacement + this.updatedText.substring(adjustedEnd);
        offset += change.replacement.length - change.original.length;
        lastAdjustedEnd = adjustedEnd;
        // console.log(offset);
    }
    console.log(['original',log_start,log_end])
    // console.log(changeLog);
  }

  function cancelPopup(){
    this.showPopup = false;
    lastAdjustedEnd = 0;
  }

  function isSlavicLetter(char) {
    const code = char.charCodeAt(0);
    return code >= 1024 && code <= 1279; // 西里尔字母的 Unicode 编码范围为 1024~1279
  }

  function isCzechLetter(char){
    const code = char.charCodeAt(0);
    return code >= 0x0100 && code <= 0x017F; // 捷克字母的 Unicode 编码范围为 1280~1327
  }
  
  function expandRangeToWordBoundaries(range) {
    //向左擴展，直到到達單詞邊界   
    while (range.startOffset > 0 && (isAlphaNumeric(range.toString()[0]) || isSlavicLetter(range.toString()[0]) || isCzechLetter(range.toString()[0]))) {
      range.setStart(range.startContainer, range.startOffset - 1);
    }
  
    if (range.startOffset > 0 && range.toString()[0] === " ") {
      range.setStart(range.startContainer, range.startOffset + 1);
    }
  
    // 向右擴展，直到到達單詞邊界
    while (range.endOffset < range.endContainer.length && (isAlphaNumeric(range.toString()[range.toString().length - 1]) || isSlavicLetter(range.toString()[range.toString().length - 1]) || isCzechLetter(range.toString()[range.toString().length - 1]))) {
      range.setEnd(range.endContainer, range.endOffset + 1);
    }
  
    if (range.endOffset < range.endContainer.length && range.toString()[range.toString().length - 1] === " ") {
      range.setEnd(range.endContainer, range.endOffset - 1);
    }

    return range;
  }
  
  function isAlphaNumeric(char) {
    return /\w/.test(char);
  }
  
  function getSelection() {
      const selection = window.getSelection();
      range = selection.getRangeAt(0);
      console.log(range);
      const rects = range.getClientRects();
  
      if (!rects.length) return null;
  
      const firstRect = rects[0];
      const lastRect = rects[rects.length - 1];
      absolutePosition = {
          x1: firstRect.left,
          y1: firstRect.top,
          x2: lastRect.right,
          y2: lastRect.bottom,
      };
      expandRangeToWordBoundaries(range);

      log_start = range.startOffset;
      log_end = range.endOffset;
      selectedText = selection.toString();
      selectedText = selection.toString();
  
      // selection.removeAllRanges();
      
    //   console.log(selectedText); 
  }
  
  function arrayToCsv(data){
    return data.map(row =>
        row
        .map(String)  // convert every value to String
        .map(v => v.replaceAll('"', '""'))  // escape double colons
        .map(v => `"${v}"`)  // quote it
        .join(',')  // comma-separated
    ).join('\r\n');  // rows starting on new lines
  }

  function exportCSV(data, filename, contentType) {
    // Create a blob
    var content = arrayToCsv(data);

    var blob = new Blob([content], { type: contentType });
    var url = URL.createObjectURL(blob);

    // Create a link to download it
    var pom = document.createElement('a');
    pom.href = url;
    pom.setAttribute('download', filename);
    pom.click();
  }
  
  export default {
  
  name: 'HelloWorld',
  props: {
    msg: String,
  },
  data(){
    return {
        rawContent: '',
        ori_content: '',
        view: 1,
        editOrOK: 0,
        isOri: false,
        tempContent: '',
        no_error: true,
        selectedfError: '',
      errorType: [
          {id:"1", name: 'Spelling Error', code: 'sp'},
          {id:"2", name: 'Syntax Error', code: 'sy'},
          {id:"3", name: 'Morphological Error', code: 'me'},
          {id:"4", name: 'Lexical Error', code: 'le'},
          {id:"5", name: 'Pronunciation Error', code: 'pr'},
          {id:"6", name: 'Punctuation Error', code: 'pu'},
          {id:"7", name: 'Stylistic Error', code: 'st'},
          {id:"8", name: 'Logical Error', code: 'lg'},
          {id:"9", name: 'Factual Error', code: 'fe'}
      ],
        menuX: 0,
        menuY: 0,
        isMenuVisible: false,
        fixedword: '',
        errors: [],
        errordict: {'Spelling Error':'#FE5F55','Syntax Error':'#59B3D9','Morphological Error':'#659157','Lexical Error':'#84569F','Pronunciation Error':'#EEC643','Punctuation Error':'#de177a','Stylistic Error':'#FAA00F','Logical Error':'#4F3824','Factual Error':'#2A4494'},
        RusData: [],
        CzeData: [],
        RusID: [],
        CzeID: [],
        doc_name: '',
        doc_name_input: '',
        currentRowIndex: 0,
        csv_error: [['id', 'error_type', 'error_original', 'error_fixed']],
        updatedText: '',
        showPopup: false,
        csv_revision: [['id', 'original_content', 'revised_content']]
    }
  },
  mounted(){
    this.preprocessText();
  },
  methods: {
        transContent: transContent,
        saveContent: saveContent,
        preStep: preStep,
        nextStep: nextStep,
        getSelection: getSelection,
        editContent: editContent,
        seeOri: seeOri,
        newError: newError,
        isSlavicLetter: isSlavicLetter,
        expandRangeToWordBoundaries: expandRangeToWordBoundaries,
        loadCSVfile: loadCSVfile,
        preprocessText: preprocessText,
        mySearchLabel: mySearchLabel,
        getRowData: getRowData,
        exportCSV: exportCSV,
        arrayToCsv: arrayToCsv,
        openPopup: openPopup,
        cancelPopup: cancelPopup,
      //   addTag: addTag,
    }
  
  }
  
  
  </script>
  
